import React, { useState } from "react";
import PropTypes from "prop-types";

const TimesheetUpdateForm = ({ initialData, onFormDataChange }) => {
  const [formData, setFormData] = useState({
    rowIndex: -1,
    Date: "",
    TaskID: initialData.TaskID,
    Account: initialData.PIC,
    StartTime: "",
    EndTime: "",
    TotalTime: "",
    Description: "",
  });

  // H√†m x·ª≠ l√Ω khi ng∆∞·ªùi d√πng thay ƒë·ªïi d·ªØ li·ªáu
  const handleChange = (e) => {
    const { name, value } = e.target;
    const updatedData = { ...formData, [name]: value };
    setFormData(updatedData);
    onFormDataChange(updatedData);
  };

  return (
    <form>
      {/* Date */}
      <div className="input-group mb-3">
        <span className="input-group-text">Date</span>
        <input type="date" className="form-control" name="Date" value={formData.Date} onChange={handleChange} />
      </div>

      {/* TaskID */}
      <div className="input-group mb-3">
        <span className="input-group-text">TaskID</span>
        <input type="text" className="form-control" name="TaskID" value={formData.TaskID} onChange={handleChange} />
      </div>

      {/* PIC */}
      <div className="input-group mb-3">
        <span className="input-group-text">Account</span>
        <input type="text" className="form-control" name="Account" value={formData.Account} onChange={handleChange} />
      </div>

      {/* Start Time */}
      <div className="input-group mb-3">
        <span className="input-group-text">Start Time</span>
        <input
          type="time"
          className="form-control"
          name="StartTime"
          value={formData.StartTime}
          onChange={handleChange}
        />
      </div>

      {/* End Time */}
      <div className="input-group mb-3">
        <span className="input-group-text">End Time</span>
        <input type="time" className="form-control" name="EndTime" value={formData.EndTime} onChange={handleChange} />
      </div>

      {/* Description */}
      <div className="input-group mb-3">
        <span className="input-group-text">Desc</span>
        <textarea className="form-control" name="Desc" value={formData.Desc} onChange={handleChange} />
      </div>
    </form>
  );
};

TimesheetUpdateForm.propTypes = {
  initialData: PropTypes.object.isRequired,
  onFormDataChange: PropTypes.func.isRequired,
};

export default TimesheetUpdateForm;
