import React from "react";
import PropTypes from "prop-types";

const Modal = ({ show, title, body, onClose, onSave }) => {
  return (
    <div
      className={`modal fade ${show ? "show d-block" : ""}`}
      tabIndex="-1"
      style={{ backgroundColor: show ? "rgba(0, 0, 0, 0.5)" : "transparent" }}
    >
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">{title}</h5>
            <button type="button" className="btn-close" onClick={onClose}></button>
          </div>
          <div className="modal-body">
            <p>{body}</p>
          </div>
          <div className="modal-footer">
            <button type="button" className="btn btn-secondary" onClick={onClose}>
              Close
            </button>
            <button type="button" className="btn btn-primary" onClick={onSave}>
              Save changes
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

Modal.propTypes = {
  show: PropTypes.bool.isRequired, // X√°c ƒë·ªãnh modal c√≥ hi·ªÉn th·ªã hay kh√¥ng
  title: PropTypes.string.isRequired, // Ti√™u ƒë·ªÅ c·ªßa modal
  body: PropTypes.string.isRequired, // N·ªôi dung b√™n trong modal
  onClose: PropTypes.func.isRequired, // H√†m x·ª≠ l√Ω khi nh·∫•n "Close"
  onSave: PropTypes.func.isRequired, // H√†m x·ª≠ l√Ω khi nh·∫•n "Save changes"
};

export default Modal;
