import React, { useEffect, useState } from "react";
import { getDataTable, convertToJSON, excelDateToJSDate, logWork, updateExcelData } from "../../js/excelFunction.js";
import Table from "../ui/Table.js";
import Emoji from "../ui/Emoji.js";
import { Button, ButtonWithIcon, ButtonWithBadge } from "../ui/Button.js";
import Modal from "../ui/Modal.js";
import TaskUpdateForm from "../ui/TaskUpdateForm.js";
import TimesheetUpdateForm from "../ui/TimeSheetUpdateForm.js";

const Task = () => {
  const [loading, setLoading] = useState(true); // Tr·∫°ng th√°i loading b·∫Øt ƒë·∫ßu l√† true
  const [data, setData] = useState([]);
  const [dataOverDue, setDataOverDue] = useState([]);
  const [dataLateStart, setDataLateStart] = useState([]);
  const [dataNotPlan, setDataNotPlan] = useState([]);
  const [account, setAccount] = useState("");
  const [total, setTotal] = useState("0");
  const [selectedOption, setSelectedOption] = useState("");
  const [modalVisible, setModalVisible] = useState(false);
  const [selectedTaskInfo, setSelectedTaskInfo] = useState(null);
  const [formDataTimesheet, setformDataTimesheet] = useState({});
  const [formDataTask, setformDataTask] = useState({});
  const [error, setError] = useState("");
  const [notStarted, setNotStarted] = useState([]);
  const [doing, setDoing] = useState([]);
  const [reviewing, setReviewing] = useState([]);
  const [waitingRelease, setWaitingRelease] = useState([]);
  const [released, setReleased] = useState([]);
  const [currentDataType, setCurrentDataType] = useState("doing"); // TraÃ£ng thaÃÅi hieÃ£ÃÇn taÃ£i cuÃâa data

  // Logic for Modal

  const handleUpdateProcess = async (taskInfo) => {
    // Ki·ªÉm tra v√† c·∫≠p nh·∫≠t gi√° tr·ªã c·ªßa taskInfo["%A.Completed"]
    if (taskInfo["%A.Completed"] >= 0 && taskInfo["%A.Completed"] < 10) {
      taskInfo["A.StartDate"] = new Date().toISOString().slice(0, 10);
      taskInfo["%A.Completed"] = 0.1;
    } else if (taskInfo["%A.Completed"] >= 10 && taskInfo["%A.Completed"] < 80) {
      taskInfo["A.EndDate"] = new Date().toISOString().slice(0, 10);
      taskInfo["%A.Completed"] = 0.8;
    } else if (taskInfo["%A.Completed"] >= 80 && taskInfo["%A.Completed"] < 90) {
      taskInfo["%A.Completed"] = 0.9;
    } else if (taskInfo["%A.Completed"] >= 90 && taskInfo["%A.Completed"] < 100) {
      taskInfo["A.ReleaseDate"] = new Date().toISOString().slice(0, 10);
      taskInfo["%A.Completed"] = 1;
    }

    // Hi·ªÉn th·ªã taskInfo sau khi c·∫≠p nh·∫≠t
    // document.getElementById("task_info").innerHTML = JSON.stringify(taskInfo);
    await updateExcelData("WBS", "TblWbs", taskInfo);
    await loadData();

    if (JSON.stringify(taskInfo) !== JSON.stringify(selectedTaskInfo)) {
      setSelectedTaskInfo(taskInfo);
    }
  };

  const handleUpdateTask = async (taskInfo) => {
    setSelectedOption("edit-task");
    setSelectedTaskInfo(taskInfo);
    setModalVisible(true);
  };
  const handleUpdateTimesheet = async (taskInfo) => {
    setSelectedOption("logwork");
    setSelectedTaskInfo(taskInfo);
    setModalVisible(true);
  };

  const processData = (jsonData) => {
    return jsonData.map((item) => {
      item["P.StartDate"] = excelDateToJSDate(item["P.StartDate"]);
      item["P.EndDate"] = excelDateToJSDate(item["P.EndDate"]);
      item["%A.Completed"] = item["%A.Completed"] * 100;

      let status;
      switch (item["%A.Completed"]) {
        case 0:
          status = <Emoji name="smile" />;
          break;
        case 0.5:
          status = <Emoji name="surprised" />;
          break;
        case 0.6:
          status = <Emoji name="crying" />;
          break;
        case 0.8:
          status = <Emoji name="sad" />;
          break;
        default:
          status = <Emoji name="dead" />;
      }

      return {
        ...item,
        // status,
        action: (
          <div class="input-group mb-3">
            <ButtonWithIcon
              type="success"
              icon="play-btn"
              size="sm"
              onClick={() => handleUpdateProcess(item)}
              title="Click to change progress"
            />
            <ButtonWithIcon
              type="warning"
              icon="pen"
              size="sm"
              onClick={() => handleUpdateTask(item)}
              title="Click to open task details"
            />
            <ButtonWithIcon
              type="danger"
              icon="stopwatch"
              size="sm"
              onClick={() => handleUpdateTimesheet(item)}
              title="Click to logwork"
            />
          </div>
        ),
      };
    });
  };

  // C·∫•u h√¨nh m√†u s·∫Øc cho c√°c c·ªôt
  const colorConfig = {
    "A.Effort": {
      red: (value, row) => value > 0 && value > row["P.Effort"],
      green: (value, row) => value > 0 && value <= row["P.Effort"],
      textWhite: (value, row) => value > 0,
    },
  };

  const groupDataByCompletion = (data) => {
    const notStarted = [];
    const doing = [];
    const reviewing = [];
    const waitingRelease = [];
    const released = [];

    data.forEach((item) => {
      if (item["%A.Completed"] === 0) {
        notStarted.push(item);
      } else if (item["%A.Completed"] >= 10 && item["%A.Completed"] < 80) {
        doing.push(item);
      } else if (item["%A.Completed"] >= 80 && item["%A.Completed"] < 90) {
        reviewing.push(item);
      } else if (item["%A.Completed"] >= 90 && item["%A.Completed"] < 100) {
        waitingRelease.push(item);
      } else if (item["%A.Completed"] === 100) {
        released.push(item);
      }
    });

    setNotStarted(notStarted);
    setDoing(doing);
    setReviewing(reviewing);
    setWaitingRelease(waitingRelease);
    setReleased(released);
  };

  /**
   * L·ªçc d·ªØ li·ªáu c√°c task qu√° h·∫°n d·ª±a tr√™n P.ReleaseDate v√† %A.Completed
   * @param {Array} data - D·ªØ li·ªáu ƒë·∫ßu v√†o ·ªü d·∫°ng JSON (danh s√°ch c√°c task)
   * @param {Date} today - Ng√†y hi·ªán t·∫°i ƒë·ªÉ so s√°nh (m·∫∑c ƒë·ªãnh l√† ng√†y hi·ªán t·∫°i)
   * @return {Array} - Danh s√°ch c√°c task qu√° h·∫°n (overdue)
   */
  const filterOverdueTasks = (data, today = new Date()) => {
    return data.filter((item) => {
      // Chuy·ªÉn ƒë·ªïi P.ReleaseDate t·ª´ ƒë·ªãnh d·∫°ng Excel sang Date c·ªßa JavaScript
      const releaseDate = excelDateToJSDate(item["P.ReleaseDate"]);

      // Ki·ªÉm tra n·∫øu P.ReleaseDate nh·ªè h∆°n ng√†y hi·ªán t·∫°i v√† %A.Completed nh·ªè h∆°n 1
      return releaseDate < today && item["%A.Completed"] < 1;
    });
  };

  /**
   * L·ªçc d·ªØ li·ªáu c√°c task c√≥ ng√†y b·∫Øt ƒë·∫ßu th·ª±c t·∫ø tr·ªÖ h∆°n so v·ªõi ng√†y b·∫Øt ƒë·∫ßu d·ª± ki·∫øn
   * @param {Array} data - D·ªØ li·ªáu ƒë·∫ßu v√†o ·ªü d·∫°ng JSON (danh s√°ch c√°c task)
   * @return {Array} - Danh s√°ch c√°c task b·∫Øt ƒë·∫ßu tr·ªÖ
   */
  const filterLateStartTasks = (data, today = new Date()) => {
    return data.filter((item) => {
      // Chuy·ªÉn ƒë·ªïi P.StartDate v√† A.StartDate t·ª´ ƒë·ªãnh d·∫°ng Excel sang Date c·ªßa JavaScript
      const plannedStartDate = excelDateToJSDate(item["P.StartDate"]);

      // Ki·ªÉm tra n·∫øu A.StartDate l·ªõn h∆°n P.StartDate (tr·ªÖ h∆°n k·∫ø ho·∫°ch)
      return today > plannedStartDate && item["%A.Completed"] === 0;
    });
  };

  const filterNotPlanTasks = (data) => {
    return data.filter((item) => {
      return item["P.Effort"] === 0 || item["P.Effort"] === "";
    });
  };

  useEffect(() => {
    loadData();
  }, []);
  useEffect(() => {
    // C·∫≠p nh·∫≠t data t∆∞∆°ng ·ª©ng v·ªõi tr·∫°ng th√°i hi·ªán t·∫°i
    switch (currentDataType) {
      case "notStarted":
        setData(notStarted);
        break;
      case "doing":
        setData(doing);
        break;
      case "reviewing":
        setData(reviewing);
        break;
      case "waitingRelease":
        setData(waitingRelease);
        break;
      case "released":
        setData(released);
        break;
      default:
        setData(doing);
    }
  }, [notStarted, doing, reviewing, waitingRelease, released, currentDataType]);

  // Handler button Load
  const loadData = async () => {
    setLoading(true);
    // T·∫£i d·ªØ li·ªáu t·ª´ b·∫£ng Excel
    const result = await getDataTable("WBS", "TblWbs");
    const jsonData = convertToJSON(result);

    // Chu·∫©n b·ªã chu·ªói account ƒë·ªÉ t√¨m ki·∫øm
    const searchString = account.trim().toLowerCase();

    // L·ªçc d·ªØ li·ªáu d·ª±a tr√™n b·∫•t k·ª≥ c·ªôt n√†o c√≥ ch·ª©a gi√° tr·ªã kh·ªõp v·ªõi searchString
    const filterData = jsonData.filter((item) => {
      // S·ª≠ d·ª•ng Object.values ƒë·ªÉ l·∫•y t·∫•t c·∫£ gi√° tr·ªã c·ªßa c√°c c·ªôt
      return Object.values(item).some((value) => {
        // Ki·ªÉm tra n·∫øu gi√° tr·ªã l√† chu·ªói v√† so s√°nh
        if (typeof value === "string") {
          return value.trim().toLowerCase().includes(searchString);
        }
        return false;
      });
    });

    // X·ª≠ l√Ω d·ªØ li·ªáu sau khi l·ªçc
    const updatedData = processData(filterData);
    groupDataByCompletion(updatedData);
    setTotal(updatedData.length);

    // Not Plan Tasks
    const notPlanData = filterNotPlanTasks(filterData);
    setDataNotPlan(notPlanData);

    //
    const overdueData = filterOverdueTasks(filterData); // G·ªçi h√†m l·ªçc qu√° h·∫°n
    setDataOverDue(overdueData);

    //
    const lateStartData = filterLateStartTasks(filterData); // G·ªçi h√†m l·ªçc b·∫Øt ƒë·∫ßu tr·ªÖ
    setDataLateStart(lateStartData);

    setLoading(false); // K·∫øt th√∫c tr·∫°ng th√°i loading
  };

  const loadDataAll = async () => {
    setAccount("");
    await loadData();
  };

  const loadDataNotPlan = () => {
    const updatedData = processData(dataNotPlan);
    setData(updatedData);
    setTotal(updatedData.length);
  };

  const loadDataOverDue = () => {
    const updatedData = processData(dataOverDue);
    setData(updatedData);
    setTotal(updatedData.length);
  };

  const loadDataLateStart = () => {
    const updatedData = processData(dataLateStart);
    setData(updatedData);
    setTotal(updatedData.length);
  };
  // Handler button Load

  const columns = [
    { header: "Idx", key: "rowIndex" },
    { header: "TaskID", key: "TaskID" },
    { header: "TaskName", key: "TaskName" },
    { header: "P.Start", key: "P.StartDate" },
    { header: "P.End", key: "P.EndDate" },
    { header: "P.Effort", key: "P.Effort" },
    { header: "A.Effort", key: "A.Effort" },
    { header: "%A.Completed", key: "%A.Completed" },
    { header: "PIC", key: "PIC" },
    { header: "Reviewer", key: "Reviewer" },
    // { header: "Status", key: "status" },
    { header: "Action", key: "action" },
  ];

  const getTitleModal = () => {
    switch (selectedOption) {
      case "logwork":
        return "Update Timesheet";
      case "edit-task":
        return "Update Task";
      default:
        return null;
    }
  };

  const getModalContent = () => {
    switch (selectedOption) {
      case "edit-task":
        return <TaskUpdateForm initialData={selectedTaskInfo} onFormDataChange={handleFormDataChange} />;
      case "logwork":
        return <TimesheetUpdateForm initialData={selectedTaskInfo} onFormDataChange={handleFormDataChange} />;
      default:
        return null;
    }
  };

  const handleFormDataChange = (updatedData) => {
    switch (selectedOption) {
      case "edit-task":
        setformDataTask(updatedData);
        break;
      case "logwork":
        setformDataTimesheet(updatedData); // L∆∞u d·ªØ li·ªáu form v√†o state
        break;
    }
  };

  const handleSave = async () => {
    switch (selectedOption) {
      case "edit-task":
        formDataTask["%A.Completed"] = formDataTask["%A.Completed"] / 100;
        await updateExcelData("WBS", "TblWbs", formDataTask);
        setSelectedTaskInfo({ ...formDataTask });
        await loadData();
        break;
      case "logwork":
        logWork(formDataTimesheet);
        await loadData();
    }
    setModalVisible(false);
  };

  // Hi·ªÉn th·ªã spinner n·∫øu ƒëang ·ªü tr·∫°ng th√°i loading

  if (loading) {
    return (
      <div className="d-flex justify-content-center">
        <p>Loading...</p>
        {/* B·∫°n c√≥ th·ªÉ th√™m spinner t√πy √Ω, v√≠ d·ª• c·ªßa Bootstrap */}
        <div className="spinner-border" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    );
  }

  // Hi·ªÉn th·ªã th√¥ng b√°o l·ªói n·∫øu c√≥ l·ªói
  if (error) {
    return <div className="alert alert-danger">{error}</div>;
  }

  return (
    <div>
      <h3>Task information:</h3>
      <p id="task_info"></p>
      <div className="input-group input-group-sm mb-3">
        <span className="input-group-text" id="basic-addon1">
          Search:
        </span>
        <input
          id="txtAccount"
          type="text"
          className="form-control"
          placeholder="Please enter the information you want to search for."
          aria-label="Username"
          aria-describedby="basic-addon1"
          value={account}
          onChange={(e) => setAccount(e.target.value)}
        ></input>
        <Button label="Load" onClick={loadData} />
        <Button label="LoadAll" type="danger" onClick={loadDataAll} />
      </div>
      <div className="input-group mb-3">
        <ButtonWithBadge label={"Total:"} type={"outline-primary"} size={"sm"} badge={total} />
        <ButtonWithBadge
          label={"‚è∞:"}
          type={"outline-primary"}
          size={"sm"}
          active={currentDataType === "notStarted" ? "active" : ""}
          badge={notStarted.length}
          onClick={() => {
            setData(notStarted);
            setCurrentDataType("notStarted");
          }}
        />
        <ButtonWithBadge
          label={"üë®‚Äçüíª:"}
          type={"outline-primary"}
          size={"sm"}
          active={currentDataType === "doing" ? "active" : ""}
          badge={doing.length}
          onClick={() => {
            setData(doing);
            setCurrentDataType("doing");
          }}
        />
        <ButtonWithBadge
          label={"üëÆ:"}
          type={"outline-primary"}
          size={"sm"}
          active={currentDataType === "reviewing" ? "active" : ""}
          badge={reviewing.length}
          onClick={() => {
            setData(reviewing);
            setCurrentDataType("reviewing");
          }}
        />
        <ButtonWithBadge
          label={"üöÄ:"}
          type={"outline-primary"}
          size={"sm"}
          active={currentDataType === "waitingRelease" ? "active" : ""}
          badge={waitingRelease.length}
          onClick={() => {
            setData(waitingRelease);
            setCurrentDataType("waitingRelease");
          }}
        />
        <ButtonWithBadge
          label={"‚úÖ:"}
          type={"outline-primary"}
          size={"sm"}
          active={currentDataType === "released" ? "active" : ""}
          badge={released.length}
          onClick={() => {
            setData(released);
            setCurrentDataType("released");
          }}
        />
      </div>
      <div className="input-group mb-3">
        {/* <button type="button" className="btn btn-sm btn-secondary" onClick={loadDataNotPlan}>
          Backlog: <span className="badge text-bg-secondary">{dataNotPlan.length}</span>
        </button> */}
        {/* <button type="button" className="btn btn-sm btn-warning" onClick={loadDataLateStart}>
          Late Start: <span className="badge text-bg-secondary">{dataLateStart.length}</span>
        </button> */}
        {/* <button type="button" className="btn btn-sm btn-danger" onClick={loadDataOverDue}>
          Overdue: <span className="badge text-bg-secondary">{dataOverDue.length}</span>
        </button> */}
      </div>
      <Table data={data} columns={columns} colorConfig={colorConfig} />
      {modalVisible && (
        <Modal
          show={modalVisible}
          title={getTitleModal()}
          body={getModalContent()}
          onClose={() => setModalVisible(false)}
          onSave={handleSave}
        />
      )}
    </div>
  );
};

export default Task;
